body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  color: white;
  background-color: #282c34;
  position: absolute;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 100%;
}

.App-header {
  background-color: #282c34;
  box-shadow: 0px 5px 4px rgba(0,0,0, .3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  overflow-wrap: break-word;
  min-height: 11%;
  min-width: 100%;
}

.aboutdiv {
  color:black;
  background-color: #f2c777;
  overflow-wrap: break-word;
  width: 100%;
}

.industry {
  margin: 1em;
  border-bottom: #fff solid 1px;
  text-align: center;
  padding: 1em 0 1em 0;
}
.industry h3 {
  margin: 0;
}
.industry h4 {
  color: #f2c777;
  margin: 0;
}
.industry h6 {
  margin: 0;
}

.industrydiv {
  vertical-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
  grid-gap: 0em;
}

.mia {
  margin: 1em 0 0 0;
  height: 2em;
  text-align: center;
}

.project {
  margin: 3px;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  -webkit-transition: 1.1s;
  transition: 1.1s;
  border: 2px solid #f2c777;

  &:hover {
    border: 2px solid #fff;
    -webkit-transition: .1s;
    transition: .1s;
  }

}
.project a {
  margin: 0;
  -webkit-transition: .5s;
  transition: .5s;
}
.project:hover a {
  color: #61dafb;
  -webkit-transition: .1s;
  transition: .1s;
}
.project h3 {
  width: inherit;
  color: #f2c777;
  -webkit-transition: .5s;
  transition: .5s;
}
.project:hover h3 {
  align-items: flex-start;
  -webkit-transition: .1s;
  transition: .1s;
}
.project h4 {
  opacity: 0;
  -webkit-transition: .5s;
  transition: .5s;
}

.project:hover h4 {
  opacity: 1;
  -webkit-transition: .2s;
  transition: .2s;
}

.project p {
  opacity: 0;
  -webkit-transition: .2s;
  transition: .2s;
}
.project:hover p {
  opacity: 1;
  -webkit-transition: .2s;
  transition: .2s;
}

.projectdiv {
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1%;
  margin: 1em;
}

.social {
  margin: 0 0 5px 0;
  display: block;
  flex-direction: row;
  text-align: center;
}
.social button {
  background-color: rgba(0,0,0,0);
  border-color: rgba(0,0,0,0);
}
.social button:hover {
  cursor: pointer;
}

.underwear{
  background-color: #282c34;
  min-width: 100%;
  height: 11%;
  min-height: 11%
}

.unused {
  margin: 3px;
  background-color: #f2c777;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #f2c777;
}

.unused h1 {
  -webkit-transform: translateY(-0.5em);
          transform: translateY(-0.5em);
}

a {
  color: white;
  text-decoration: none;
  font-style: bold;
}

svg {
  padding: 0 5px 0 5px;
  -webkit-filter: invert(100%);
  filter: invert(100%);
}

